/* Styling for the body of the Employee Portal */
.bodyEmployee {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    height: 100vh;
    background: linear-gradient(to bottom, #fff 50%, #fff 50%);
}

/* Styling for the company logo container */
.logContainer{
    display: flex;
    justify-content: flex-start;
    align-items: center;
    width: 100%;
    padding: 10px 0;   
}

/* Styling for the company logo image */
.logeCont{
    width: 180px; 
    height: auto; 
    margin-left: 10px; 
} 

/* Styling for the admin portal section */
.admin-portal {
    font-family: 'Arial', sans-serif;
    color: #fff;
    background-color: #006837;
    padding: 0;
    margin-top: 20px;
    height: 230px;
}

/* Styling for the header within the admin portal */
.header2 {
    background-color: #006837;
    padding: 20px;
    text-align: center;
}

/* Styling for the menu bar */
.menu-bar {
    display: flex;
    justify-content: space-around;
    align-items: center;
    background-color: #0ae1bd;
    padding: 10px 0;
    margin: 0 auto;
    max-width: 1000px;
    border-radius: 20px;
    box-shadow: 0 4px 4px rgba(0, 0, 0, 0.2);
}

/* Styling for the menu buttons */
.menu-button {
    background-color: #6C63FF;
    border: none;
    border-radius: 20px;
    padding: 10px 30px;
    margin: 0 5px;
    color: white;
    text-transform: uppercase;
    cursor: pointer;
    transition: background-color 0.3s ease;
    box-shadow: 0 4px 4px rgba(0, 0, 0, 0.2);
}

/* Styling for the menu buttons on hover */
.menu-button:hover {
    background-color: #7C7AFF;
}

/* Styling for the welcome message */
.welcome-message {
    background-color: #032528;
    color: #fff;
    padding: 20px;
    text-align: center;
    margin-top: 200px;
    box-shadow: 0 4px 4px rgba(0, 0, 0, 0.2);
}
